import React, {Component} from 'react';
import s from "./../../Profile.module.css";
import {Field, reduxForm} from "redux-form";
import {maxLengthControlerConstructor, required} from "../../../../utils/validators";
import {createField, Textarea} from "../../../common/FormsControls/FormsControls";
import Button from "@material-ui/core/Button";
import {Grid} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import userPhoto from "../../../../assets/images/avatarImg.png";
import Preloader from "../../../common/Preloader/Preloader";

const maxLength20 = maxLengthControlerConstructor(20)
const useStyles = makeStyles((theme) => ({
    form: {
        width: '100%',
        padding: '20px'
    },
    addPost: {
        textAlign: 'center'
    },
    addPostBlock: {
        width: '100%',
        paddingLeft: '75px',
        alignSelf: 'center',
        position: 'relative'
    },
    addPostTextarea: {
        width: '100% !important',
        border: '1px solid #ccc',
        color: '#939598',
        padding: '6px 12px',
        borderRadius: '4px'
    },
    large: {
        width: theme.spacing(7),
        height: theme.spacing(7),
        position: 'absolute',
        top: '0',
        left: '0'
    }
}))


const AddPostForm = (props) => {
    const classes = useStyles()

    return (
        <form className={classes.form} onSubmit={props.handleSubmit}>
            <Grid container alignItems='center'>
                <Grid item xs={12} sm={9} className={classes.addPostBlock}>
                    <Avatar alt="Remy Sharp"
                            className={classes.large}
                            src={props.profile.photos.large != null ?
                                props.profile.photos.large : userPhoto} />
                    {createField("Write what you wish", "newPostText",
                        [required, maxLength20], Textarea,
                        "",{className: classes.addPostTextarea})}
                </Grid>
                <Grid item xs={12} sm={3} className={classes.button}>
                    <Button variant='contained' color='primary' type='submit'>Add Post</Button>
                </Grid>
            </Grid>
        </form>
    )
}


const AddPostReduxForm = reduxForm({form: "AddPostForm"})(AddPostForm);

const AddPost = (props) => {

    const classes = useStyles()
    let onAddPost = (values) => {
        props.addPost(values.newPostText);
    };
    if (!props.profile) {
        return <Preloader/>
    }

    return (<div className={classes.addPost} >
        <AddPostReduxForm profile={props.profile} onSubmit={onAddPost}/>
    </div>);
}

export default AddPost;
