import React from "react";
import {Field, reduxForm} from "redux-form";
import {createField, Input} from "../common/FormsControls/FormsControls";
import {required} from "../../utils/validators";
import {connect} from "react-redux";
import {login} from "../redux/Auth-reducer";
import {Redirect} from "react-router-dom";
import Card from "@material-ui/core/Card";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Alert from "@material-ui/lab/Alert";

const useStyles = makeStyles((theme) => ({
    root: {
        // maxWidth: '50vw',
        maxWidth: '575px',
        textAlign: 'center',
        margin: '10vh 10px',
        [theme.breakpoints.up('sm')]: {
            margin: '10vh auto',
        },
        [theme.breakpoints.up('md')]: {
            margin: '100px auto',
        },
        padding: '30px',
    },
    form: {
        marginTop: '20px'
    },
    input: {
        width: '70%',
        padding: '10px 0'
    },
    button: {
        marginTop: '20px'
    },
    formSummaryError: {
        color: 'black',
        border: '1px solid red',
        padding: '5px',
        backgroundColor: 'inherit'
    },
    alert: {
        textAlign: 'left'
    }
}))

const validate = values => {
    const errors = {}
    const requiredFields = [
        'password',
        'email'
    ]
    requiredFields.forEach(field => {
        if (!values[field]) {
            errors[field] = 'Required'
        }
    })
    if (
        values.email &&
        !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
        errors.email = 'Invalid email address'
    }
    return errors
}
const renderTextField = ({input, meta: {touched, error}, ...custom}) => (
    <TextField
        errorText={touched && error}
        helperText={touched && error}
        {...input}
        {...custom}
    />
)
const renderCheckbox = ({input, label}) => (
    <div>
        <FormControlLabel
            control={
                <Checkbox
                    checked={input.value ? true : false}
                    onChange={input.onChange}
                />
            }
            label={label}
        />
    </div>
)

const LoginForm = ({handleSubmit, error, captchaURL}) => {
    const classes = useStyles()

    return (
        <form className={classes.form} onSubmit={handleSubmit} autoComplete="off">
            <Field className={classes.input} placeholder='Email' name='email' label='Email'
                   component={renderTextField}/>
            <Field className={classes.input} placeholder='Password' name='password' type='password' label='Password'
                   component={renderTextField}/>
            <Field name="rememberMe" component={renderCheckbox} label="remember me"/>
            {/*{createField("Email", "email", [required], Input)}*/}
            {/*{createField("Password", "password", [required], Input, null, {type: "password"})}*/}
            {/*{createField(null, "rememberMe", [], Input, "remember me", {type: "checkbox"})}*/}
            {error && <div className={classes.formSummaryError}>{error}</div>}

            {captchaURL && <img src={captchaURL}/>}
            {captchaURL && createField("Symbols of captcha", "captcha", [required], Input)}
            <div>
                <Button size='large' variant='contained' type='submit' color='primary'
                        className={classes.button}>Login</Button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({form: "login", validate})(LoginForm);

const LoginPage = ({login, isAuth, captchaURL}) => {
    const classes = useStyles()

    let onSubmit = (data) => {
        login(data.email, data.password, data.rememberMe, data.captcha)
    }

    if (isAuth) {
        return <Redirect to={"/profile"}/>
    }

    return (<div>
            <Card className={classes.root}>
                <Alert className={classes.alert} severity="error">
                    Login test data: <br/>
                    email: om.sergiy@gmail.com, password: 11111
                </Alert>
                <Typography className={classes.login} variant="h4">Login</Typography>
                <LoginReduxForm onSubmit={onSubmit} captchaURL={captchaURL}/>
            </Card>

        </div>
    )
}

let mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth,
        captchaURL: state.auth.captchaURL
    };
};

export default connect(mapStateToProps, {login})(LoginPage);
