import React from 'react'
import clsx from 'clsx'
import {makeStyles} from '@material-ui/core/styles'
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'
import Button from '@material-ui/core/Button'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import {NavLink} from "react-router-dom"
import MenuIcon from "@material-ui/icons/Menu";

const useStyles = makeStyles({
    list: {
        width: '250px',
    },
    fullList: {
        width: 'auto',
    },
});

export default function SwipeableTemporaryDrawer() {
    const classes = useStyles();
    const [state, setState] = React.useState(false);

    const toggleDrawer = (open) => () => {
       setState(open);
    }

    const list = () => (
        <div
            className={classes.list}
            role="presentation"
            onClick={toggleDrawer(false)}
            onKeyDown={toggleDrawer( false)}
        >
            <List>
                <ListItem button>
                    <NavLink to='/profile' className={classes.title}
                             activeClassName={classes.activeLink}>Profile</NavLink>
                </ListItem>
                <ListItem button>
                    <NavLink to='/dialogs' className={classes.title}
                             activeClassName={classes.activeLink}>Messeges</NavLink>
                </ListItem>
                <ListItem button>
                    <NavLink to='/users' className={classes.title} activeClassName={classes.activeLink}>Users</NavLink>
                    {/*<NavLink to='/butl' className={classes.title} activeClassName={classes.activeLink}>БАТЛ</NavLink>*/}
                    {/*<NavLink to='/game' className={classes.title} activeClassName={classes.activeLink}>Game</NavLink>*/}
                </ListItem>
                <ListItem button>
                    <NavLink to='/music' className={classes.title} activeClassName={classes.activeLink}>Music</NavLink>
                </ListItem>
            </List>
        </div>
    )
const anchor = 'left'
    return (
        <div>
                  <React.Fragment>
                    <Button onClick={toggleDrawer(true)}><MenuIcon/></Button>
                    <SwipeableDrawer
                        anchor={anchor}
                        open={state}
                        onClose={toggleDrawer( false)}
                        onOpen={toggleDrawer( true)}
                    >
                        {list()}
                    </SwipeableDrawer>
                </React.Fragment>
        </div>
    )
}
