import React from 'react';
import s from "./Profile.module.css";
import Posts from './Posts/Posts';
import ProfileBackground from './ProfileBackground/ProfileBackground';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import ProfilePorftfolio from './ProfilePorftfolio/ProfilePorftfolio';
import {connect} from "react-redux";
import {
    addPost,
    requestStatus,
    getUserProfile,
    setPhoto,
    updateProfileData,
    updateNewPost,
    updateStatus
} from "../redux/Profile-reducer";
import {withRouter} from "react-router-dom";
import {compose} from "redux";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {getIsAuth, getNewPostText, getPosts, getProfile, getStatus, getUserId} from "../redux/Profile-selectors";
import {Field} from "redux-form";
import {createField, Input} from "../common/FormsControls/FormsControls";
import {required} from "../../utils/validators";
import Button from "@material-ui/core/Button";
import ProfileMUI from "./ProfileMUI";


class ProfileContainer extends React.Component {

    refreshProfile() {
        let userId = this.props.match.params.userId
        if (!userId) {
            if (!this.props.userIdOwner) {
                userId = 2
            } else {
                userId = this.props.userIdOwner
            }
        }
        this.props.getUserProfile(userId)
        this.props.requestStatus(userId)
    }

    componentDidMount() {
        this.refreshProfile()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        // todo что делает match?
        if (this.props.match.params.userId != prevProps.match.params.userId) {
            this.refreshProfile();
        }
    }

    render() {
        return (<div>
            {/*todo как прокинуть все пропсі */}
            <ProfileMUI profile={this.props.profile} status={this.props.status}
                        updateProfileData={this.props.updateProfileData}
                        updateStatus={this.props.updateStatus} setPhoto={this.props.setPhoto}
                        addPost={this.props.addPost}
                        updateNewPost={this.props.updateNewPost}
                        newPostText={this.props.newPostText}
                        posts={this.props.posts}/>
            {/*<ProfileBackground />*/}
            {/*<ProfileInfo profile={this.props.profile} status={this.props.status}*/}
            {/*             updateProfileData={this.props.updateProfileData}*/}
            {/*             updateStatus={this.props.updateStatus} setPhoto={this.props.setPhoto}*/}
            {/*/>*/}
            {/*<Posts addPost={this.props.addPost}*/}
            {/*       updateNewPost={this.props.updateNewPost}*/}
            {/*       newPostText={this.props.newPostText}*/}
            {/*       posts={this.props.posts}*/}
            {/*/>*/}
            {/*/>*/}
            {/*<ProfilePorftfolio/>*/}
        </div>);
    }

}

let mapStateToProps = (state) => {

    return {
        newPostText: getNewPostText(state),
        posts: getPosts(state),
        profile: getProfile(state),
        auth: getIsAuth(state),
        userIdOwner: getUserId(state),
        status: getStatus(state),
    };
};

// let WithUrlDataContainerComponent = withRouter(ProfileContainer);
//
// export default connect(mapStateToProps, {addPost, updateNewPost, getUserProfile})(WithUrlDataContainerComponent);
export default compose(
    connect(mapStateToProps, {
        addPost,
        updateNewPost,
        getUserProfile,
        requestStatus: requestStatus,
        updateStatus,
        setPhoto,
        updateProfileData
    }),
    withRouter,
    withAuthRedirect
)(ProfileContainer)
