import React from 'react'
import Typography from "@material-ui/core/Typography"
import Button from "@material-ui/core/Button"
import {makeStyles} from "@material-ui/core/styles"

const useStyles = makeStyles({
    root: {
        textAlign: 'center',
    }
})

function ProfileData(props) {
    const classes = useStyles()
    let {fullName, aboutMe, lookingForAJob, lookingForAJobDescription, ...profile} = props.profile;

    return (
        <div className={classes.root}>

            <Typography variant='h6'>
                {fullName}
            </Typography>
            <Typography variant='subtitle1'>
                {aboutMe}
            </Typography>
            <Typography variant='subtitle2'>
                {lookingForAJob
                    ? <div>
                        Looking for a job now <br/>
                        Description: {lookingForAJobDescription}
                    </div>
                    : "Not looking for a job now"}
            </Typography>
            <div>
                <Button variant='outlined' onClick={props.activateEditMode}>Edit profile</Button>
            </div>
        </div>
    )
}


export default ProfileData;
