import React from 'react';
// import s from "./../Profile.module.css";
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";


const useStyles = makeStyles({
    root: {
      padding: '10px'
    },
    title: {
        color: '#6d6e71',
        fontSize: '18px'
    },
    imgBlock: {
        '& img': {
            width: '100%',
            padding: '5px'
        }
    }
});

const RandomPhotos = () => {
    const classes = useStyles()

    return (
        <div className={classes.root}>
            <div className={classes.title}>
                Random photos
            </div>
            <Grid container className={classes.imgBlock}>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/200/200" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/201/201" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/202/202" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/203/203" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/204/204" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/205/205" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/206/206" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/207/207" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/208/208" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/209/209" alt=""/></Grid>
                <Grid item xs={6} sm={4}><img src="https://picsum.photos/210/210" alt=""/></Grid>
            </Grid>
        </div>);
}

export default RandomPhotos;
