import React from 'react';
import s from "./Profile.module.css";
import Posts from './Posts/Posts';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";
import Preloader from "../common/Preloader/Preloader";
import RandomPhotos from "./ProfilePorftfolio/ProfilePorftfolio";

const useStyles = makeStyles({
    backgroundImgBlock: {
        width: '100%',
        height: '50vh',
        background: 'url("https://picsum.photos/1000/300") no-repeat',
        backgroundPosition: 'center',
        backgroundSize: 'cover'
    },
    profileBlock: {
        position: 'relative',
        top: '-135px'
    }
});

export default function ProfileMUI(props) {
    const classes = useStyles()

    if (!props.profile) {
        return <Preloader/>
    }

    return (<Grid container justify='center' className={classes.root}>
        <Grid item xs={12} className={classes.backgroundImgBlock}>
        </Grid>
        <Grid item xs={12} md={3} className={classes.profileBlock}>
            <ProfileInfo  {...props}/>
        </Grid>
        <Grid item xs={12} md={6}>
            <Posts {...props}/>
        </Grid>
        <Grid item xs={12} md={3}>
            <RandomPhotos />
        </Grid>


    </Grid>);
}
