import React from 'react';
import s from "./../Profile.module.css";
import AddPost from "./AddPost/AddPost";
import MyPosts from "./MyPosts/MyPosts";
import userPhoto from "../../../assets/images/avatarImg.png";
import Post from "./MyPosts/Post";

const Posts = (props) => {

    const items = props.posts.sort(function (a, b) {
        return b.id - a.id
    })
    const p = items.map(el => (
        <Post
            userName={props.profile.fullName}
            userAvatarImg={props.profile.photos.large != null ? props.profile.photos.large : userPhoto}
            message={el.message}
            likesCount={el.likesCount}
        />))

    return (<div className={s.profilePosts}>
        <AddPost addPost={props.addPost}
                 updateNewPost={props.updateNewPost}
                 newPostText={props.newPostText}
                 profile={props.profile}
        />
        {p}
        {/*<MyPosts posts={props.posts}/>*/}
    </div>);
};

export default Posts;
