import {authAPI, securityAPI} from "../../api/api";
import {stopSubmit} from "redux-form";

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaURL: null
};

const SET_AUTH_USER_DATA = "project/auth/SET-AUTH-USER-DATA";
const GET_CAPTCHA_URL_SUCCESS = "project/auth/GET_CAPTCHA_URL_SUCCESS";

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER_DATA:
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                ...action.payload
            }
        default:
            return state;
    }
};

export let setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_AUTH_USER_DATA,
    payload: {userId, email, login, isAuth}
});
export const getAuthUserData = () => async (dispatch) => {
    let response = await authAPI.authMe();
    if (response.resultCode === 0) {
        let {id, email, login} = response.data;
        dispatch(setAuthUserData(id, email, login, true));
    }

}
export let getCaptchaUrlSuccess = (captchaURL) => ({
    type: GET_CAPTCHA_URL_SUCCESS, payload: {captchaURL}
});

export const login = (email, password, rememberMe, captcha) => {
    return async (dispatch) => {
        let response = await authAPI.login(email, password, rememberMe, captcha);
            if (response.resultCode === 0) {
                dispatch(getAuthUserData());
            } else {
                if (response.resultCode === 10) {
                    dispatch(getCaptchaURL());
                }
                let message = response.messages.length > 0 ? response.messages[0] : "Some error";
                dispatch(stopSubmit('login', {_error: message}));
            }
    }
};
export const logout = () => (dispatch) => {
    authAPI.logout().then(response => {
        if (response.resultCode === 0) {
            dispatch(setAuthUserData(null, null, null, false));
        }
    })
}
export const getCaptchaURL = () => async (dispatch) => {
    let response = await securityAPI.getCapthaURL();

    const captchaURL = response.data.url;
    dispatch(getCaptchaUrlSuccess(captchaURL));


}

export default authReducer;
