import React, {useState} from 'react';
import s from "./../Profile.module.css";
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatus/ProfileStatusWithHooks";
import ProfileData from "./ProfileData";
import ProfileDataEditForm from "./ProfileDataEditForm";
import ProfileMainPhoto from "./ProfileMainPhoto";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles({
    profileLogo: {
        textAlign: 'center',
        position: 'relative',
        // top: '-125px'
    }
});

const ProfileInfo = (props) => {

    const {profile, setPhoto, updateMode, updateProfileData} = props
    const classes = useStyles()
    const [editMode, setEditMode] = useState(false)

    const activateEditMode = () => {
        setEditMode(true)
    }
    const onSubmit = (formData) => {
        updateProfileData(formData);
        setEditMode(false)
    }

    if (!profile) {
        return <Preloader/>
    }

    return (
        <div className={classes.profileInfo}>
            <ProfileMainPhoto updateMode={updateMode} profile={profile} setPhoto={setPhoto}/>
            {!editMode
                ? <ProfileData profile={profile} activateEditMode={activateEditMode}/>
                : <ProfileDataEditForm initialValues={profile} onSubmit={onSubmit}/>
            }
        </div>);
}




export default ProfileInfo;
