import React from 'react'
import {NavLink} from 'react-router-dom'
import AppBar from "@material-ui/core/AppBar"
import Toolbar from "@material-ui/core/Toolbar"
import IconButton from "@material-ui/core/IconButton"
import Typography from "@material-ui/core/Typography"
import MenuIcon from '@material-ui/icons/Menu'
import Button from "@material-ui/core/Button"
import {makeStyles} from "@material-ui/core/styles"
import SwipeableTemporaryDrawer from "./SwipeableTemporaryDrawer";

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: '#231F20',
        position: 'relative'
    },
    toolbar: {
        justifyContent: 'flex-end'
    },
    menuButton: {
        position: 'absolute',
        top: '0',
        right: '0'
    },
    title: {
        cursor: 'pointer',
        fontFamily: '\'Lato\', sans-serif',
        fontWeight: '600',
        fontSize: '13px',
        color: 'white',
        paddingRight: '20px'
    },
    activeLink: {
        color: '#27aae1'
    },
    navBlock: {
        display: 'none',
        [theme.breakpoints.up('sm')]: {
            display: 'block',
        },
    },
    drawer: {
        display: 'block',
        position: 'absolute',
        left: '0',
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },
    }
}));

const Navbar = ({isAuth, logout}) => {
    const classes = useStyles()

    return (
        <AppBar position="static" className={classes.root}>
            <Toolbar className={classes.toolbar}>
                <div className={classes.drawer}>
                    <SwipeableTemporaryDrawer/>
                </div>
                <div className={classes.navBlock}>
                    <NavLink to='/profile' className={classes.title} activeClassName={classes.activeLink}>Profile</NavLink>
                    {/*<NavLink to='/dialogs' className={classes.title} activeClassName={classes.activeLink}>Messeges</NavLink>*/}
                    <NavLink to='/users' className={classes.title} activeClassName={classes.activeLink}>Users</NavLink>
                    {/*<NavLink to='/butl' className={classes.title} activeClassName={classes.activeLink}>БАТЛ</NavLink>*/}
                    {/*<NavLink to='/game' className={classes.title} activeClassName={classes.activeLink}>Game</NavLink>*/}
                    {/*<NavLink to='/music' className={classes.title} activeClassName={classes.activeLink}>Music</NavLink>*/}
                </div>
                {
                    isAuth
                        ? <Button color='primary' variant="outlined" onClick={logout}>
                            Logout
                        </Button>
                        : <Button variant="contained">
                            <NavLink to='/login'>
                                Login
                            </NavLink>
                        </Button>
                }
            </Toolbar>
        </AppBar>
    )
}

export default Navbar;
