import React from 'react'
import './App.css'
import {BrowserRouter, HashRouter, Route, Switch, withRouter} from 'react-router-dom'
import HeaderContainer from "./components/Header/HeaderContainer"
import DialogsContainer from "./components/Dialogs/DialogsContainer"
import LoginPage from "./components/LoginPage/LoginPage"
import {connect, Provider} from "react-redux"
import {compose} from "redux"
import {initializeApp} from "./components/redux/App-reducer"
import Preloader from "./components/common/Preloader/Preloader"
import store from "./components/redux/redux-store"
import {withSuspense} from "./hoc/withSuspense"
import CssBaseline from "@material-ui/core/CssBaseline"
import NavbarContainer from "./components/Navbar/NavbarContainer"
import Butl from "./components/Butl/Butl";
import Game from "./components/Game/game";
import Page404 from "./components/page404/page404";
import ProfileContainer from "./components/Profile/ProfileContainer";

// React.lazy - дает возможность не загружать компонент. Этот компонент будет загружаться только при его вызове.
// Используется для тех компонент которые с большой вероятностью редко используются

const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'));
// const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));

// setInterval(() => {store.dispatch({type: "FAKE"})}, 1000);


class App extends React.Component {
    catchAllUnhandledErrors = (promiseRejectionEvent) => {
        alert("some errors occured")
    }

    componentDidMount() {
        this.props.initializeApp();
        window.addEventListener("unhandledrejection", this.catchAllUnhandledErrors);
    }

    componentWillUnmount() {
        window.removeEventListener("unhandledrejection", this.catchAllUnhandledErrors);
    }

    render() {
        // пока приложение не инициализировалось будет отображаться крутилка
        if (!this.props.initialized) {
            return <Preloader/>
        }
        return (
            <div className="app-wrapper">
                {/*<HeaderContainer/>*/}
                {/*<Navbar/>*/}
                <NavbarContainer />
                <div className="app-wrapper-content">
                    <Switch>
                        <Route path="/dialogs" render={() => <DialogsContainer/>}/>
                        {/*Разобраться с валидацией полей*/}
                        <Route path="/login" render={() => <LoginPage/>}/>
                        <Route path="/butl" render={() => <Butl/>}/>
                        <Route path="/game" render={() => <Game/>}/>
                        <Route path="/users" render={withSuspense(UsersContainer)}/>
                        {/*<React.Suspense fallback={<Preloader/>}>*/}
                            <Route path="/profile/:userId?" render={() => <ProfileContainer/>}/>
                        {/*</React.Suspense>*/}
                        <Route exact path="/" render={() => <ProfileContainer/>}/>
                        <Route render={() => <Page404 />}/>
                        {/*exact - url должен совпадать точь-в-точь, только /login, такой путь /login/facebook отображаться не будет*/}

                    </Switch>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
});

const AppContainer = compose(withRouter,
    connect(mapStateToProps, {initializeApp}))
(App)

const SamuraiJSApp = () => {
    // HashRouter использовали для нормального отображения на гитхабе (при нем добавляется в адресной строке "#" и это позволяет корректно на гитхабе отображать
    // но лучше использовать BrowserRouter
    return <HashRouter basename={process.env.PUBLIC_URL}>
        {/*нужен для react-redux. Provider прокидывает store внутрь для всех компонет для его доступности,
         так прокидываются только глобал вещи, напр: язык сайта, тема (черная, светлая)*/}
        <Provider store={store}>
            <CssBaseline />
            <AppContainer/>
        </Provider>
    </HashRouter>
}

export default SamuraiJSApp;
