import React, {useEffect, useState} from 'react';
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 140
    },
    name: {
        height: '65px'
    },
    img: {
        maxWidth: '400px'
    }
});

const Butl = () => {
    let [SashaPoint, plusSashaPoint] = useState(0)
    let [SergiyPoint, plusSergiyPoint] = useState(0)
    let [imgSasha, changeSashaImg] = useState(400)
    let [imgSergiy, changeSergiyImg] = useState(500)
    const classes = useStyles();
    const win = () => {
        if (SashaPoint == 9) {
            alert("Саша перемогла")
        }
        if (SergiyPoint == 9) {
            alert("Сергій переміг")
        }
    }


    return (
        <Grid container>
            <Grid item justifySelf='center' xs={6}>
                <div>
                    <h1>Саша: {SashaPoint}</h1>
                </div>
                <div className={classes.img}>
                    <img className={classes.img} src={`https://picsum.photos/${imgSasha}/${imgSasha}`}/>
                </div>

                <div>
                    <Button onClick={() => {
                        plusSashaPoint(SashaPoint + 1)
                        changeSashaImg(imgSasha + 1)
                        changeSergiyImg(imgSergiy + 1)
                        win()
                    }} variant="contained" color="primary"> Саша виграла</Button>
                </div>

            </Grid>
            <Grid item xs={6}>
                <h1>Сергій: {SergiyPoint}</h1>
                <div className={classes.img}>
                    <img className={classes.img} src={`https://picsum.photos/${imgSergiy}/${imgSergiy}`} alt=""/>
                </div>
                <div>
                    <Button
                        onClick={() => {
                            plusSergiyPoint(SergiyPoint + 1)
                            changeSashaImg(imgSasha + 1)
                            changeSergiyImg(imgSergiy + 1)
                            win()
                        }}
                        variant="contained" color="secondary"> Сергій виграв</Button>
                </div>

            </Grid>

        </Grid>
    );
}

export default Butl;
