import React, {Component, useState} from 'react';
import s from "./../Profile.module.css";
import userPhoto from "../../../assets/images/avatarImg.png";
import Preloader from "../../common/Preloader/Preloader";
import Button from "@material-ui/core/Button";
import {makeStyles} from "@material-ui/core/styles"
import EditIcon from '@material-ui/icons/Edit';
import Fab from "@material-ui/core/Fab";
import IconButton from "@material-ui/core/IconButton";

const useStyles = makeStyles({
    root: {
        textAlign: 'center',
        position: 'relative',
        // top: '-125px'
    },
    img: {
        width: '100%',
        borderRadius: '50%',
        border: '10px solid white'
    },
    iconBlock: {
        position: 'absolute',
        right: '30%',
        transform: 'translateX(50%)',
        bottom: '0',
        cursor: 'pointer'
    },
    imgBlock: {
        position: 'relative',
        width: '180px',
        margin: 'auto'
    },
    input: {
        display: 'none'
    }
})


const ProfileMainPhoto = (props) => {
    const classes = useStyles()
    const [editMode, setEditMode] = useState(false)

    const mainPhotoSelected = (e) => {
        if (e.currentTarget.files.length) {
            setEditMode(true)
            props.setPhoto(e.currentTarget.files[0])
            setEditMode(false)
        }
    }

    return (
        <div className={classes.root}>
            <div className={classes.imgBlock}>{!editMode
                ? <img className={classes.img}
                       src={props.profile.photos.large != null ? props.profile.photos.large : userPhoto}
                       alt="logo"/>
                : <Preloader/>
            }
                <div className={classes.iconBlock}>
                    <input onChange={mainPhotoSelected} accept="image/*" className={classes.input} id="icon-button-file"
                           type="file"/>
                    <label className={classes.iconButton} htmlFor="icon-button-file">
                        <IconButton color="primary" aria-label="upload picture" component="span">
                            <EditIcon/>
                        </IconButton>
                    </label>
                </div>
            </div>
        </div>
    )
}


export default ProfileMainPhoto;
