import React, {useEffect, useState} from 'react';
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {makeStyles} from "@material-ui/core/styles"
import img0 from "../../assets/images/game/paper.jpg"
import img1 from "../../assets/images/game/kolodec.png"
import img2 from "../../assets/images/game/scissors.jpg"
import img3 from "../../assets/images/game/stone.png"
import defaultImg from "../../assets/images/game/default.jpg"
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 140
    },
    name: {
        height: '65px'
    },
    img: {
        maxWidth: '200px'
    },
    buttonBlock: {
        padding: '20px 0'
    },
    imgChoose: {
        maxWidth: '100px',
        border: '3px solid grey',
        cursor: 'pointer'
    }
});

const Game = () => {
    let [SashaResult, plusSashaResult] = useState(0)
    let [SergiyResult, plusSergiyResult] = useState(0)
    let [SashaPoint, changeSashaPoint] = useState(-1)
    let [SergiyPoint, changeSergiyPoint] = useState(-1)
    let [imgSasha, changeSashaImg] = useState(defaultImg)
    let [imgSergiy, changeSergiyImg] = useState(defaultImg)

    const classes = useStyles();

    function result() {

        const sashaWin = () => {
            alert("Саша виграла")
            plusSashaResult(SashaResult + 1)
        }
        const sashaLose = () => {
            alert('Компютер виграв')
            plusSergiyResult(SergiyResult + 1)
        }
        const draw = () => {
            alert('Нічия, пробуйте ще раз')
        }


        if (SashaPoint === 0) {
            if (SergiyPoint === 1 || SergiyPoint === 3) {
                sashaWin()
            } else if (SergiyPoint == 0) {
                draw()
            } else {
                sashaLose()
            }
        } else if (SashaPoint == 1) {
            if (SergiyPoint == 2 || SergiyPoint === 3) {
                sashaWin()
            } else if (SergiyPoint == 1) {
                draw()
            } else {
                sashaLose()
            }
        } else if (SashaPoint == 2) {
            if (SergiyPoint == 1 || SergiyPoint == 0) {
                sashaWin()
            } else if (SergiyPoint == 2) {
                draw()
            } else {
                sashaLose()
            }
        } else if (SashaPoint == 3) {
            if (SergiyPoint == 2) {
                sashaWin()
            } else if (SergiyPoint == 3) {
                draw()
            } else {
                sashaLose()
            }
        }
        changeSashaImg(defaultImg)
        changeSashaPoint(-1)
        changeSergiyImg(defaultImg)


    }

    function computerChoose(i) {
        // let i = Math.floor(Math.random() * Math.floor(4))
        changeSergiyPoint(i)
        let a = changeCompImg(i)
        changeSergiyImg(a)
        alert('point: ' + SergiyPoint)
    }

    const start = (s) => {

        let i = Math.floor(Math.random() * Math.floor(4))
        changeSergiyImg(changeCompImg(i))
        changeSergiyPoint(i)

        setTimeout(result, 1000)
    }
    const changeCompImg = (i) => {
        if (i == 0) {
            return img0
        } else if (i == 1) {
            return img1
        } else if (i == 2) {
            return img2
        } else if (i == 3) {
            return img3
        }
    }

    return (
        <Grid container justify='center'>
            <Grid item xs={12}>
                {SashaPoint} ----- {SergiyPoint}
            </Grid>
            <Grid item justifySelf='center' xs={6}>
                <div>
                    <h1>Саша: {SashaResult}</h1>
                </div>
                <div className={classes.img}>
                    <img className={classes.img} src={imgSasha}/>
                </div>

            </Grid>
            <Grid item xs={6}>
                <h1>Компьютер: {SergiyResult}</h1>
                <div className={classes.img}>
                    <img className={classes.img} src={imgSergiy} alt=""/>
                </div>

            </Grid>
            <Grid item xs={12} className={classes.buttonBlock} container justify='center'>
                <Button

                    onClick={() => {
                        // if (SashaPoint == -1) {
                        //     alert('Виберіть ваш варіант')
                        // } else {
                        //computerChoose(Math.floor(Math.random() * Math.floor(4)))
                        //console.log(SergiyPoint)
                        // alert(SergiyPoint)
                        result()
                        // }

                    }}
                    variant="contained" color="secondary"> Старт</Button>
            </Grid>

            <Grid item container justify='center' xs={12}>
                <Grid item xs={12}>
                    <Typography variant='h3'>Роби свій вибір: </Typography>
                </Grid>

                <Grid item xs={3}
                      onClick={() => {
                          changeSashaImg(img0)
                          changeSashaPoint(0)
                          start(0)
                      }}>
                    <img className={classes.imgChoose} src={img0} alt=""/>
                </Grid>
                <Grid item xs={3}
                      onClick={() => {
                          changeSashaImg(img1)
                          changeSashaPoint(1)
                          start()
                      }}>
                    <img className={classes.imgChoose} src={img1} alt=""/>
                </Grid>
                <Grid item xs={3}
                      onClick={() => {
                          changeSashaImg(img2)
                          changeSashaPoint(2)
                          start(2)
                      }}>
                    <img className={classes.imgChoose} src={img2} alt=""/>
                </Grid>
                <Grid item xs={3}
                      onClick={() => {
                          changeSashaImg(img3)
                          changeSashaPoint(3)
                          start(3)
                      }}>
                    <img className={classes.imgChoose} src={img3} alt=""/>
                </Grid>


            </Grid>
        </Grid>
    );
}

export default Game;
